.paginator {
    margin: 10px
}

.pageNumber {
    padding: 5px;
    border: 1px solid grey;
    margin: 1px;
    background-color: bisque;
    color: black;
    border-radius: 5px;
    cursor: pointer
}


.pageNumber.selectedPage {
    font-weight: bold;
    border-color: black
}

.button {
    background-color: rgba(220, 185, 103, 0.49);
    margin-left: 5px;
    border: 1px solid grey;
    border-radius: 3px;
margin-right: 5px;
    cursor: pointer;
}